import { useMemo } from "react";
import styles from "./SectionCard.module.css";

const SectionCard = ({
  productImageId,
  price,
  productCategory,
  shoeModel,
  propWidth,
}) => {
  const divStyle = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  return (
    <div className={styles.pdpInner}>
      <div className={styles.screenShot20231213At610Parent}>
        <img
          className={styles.screenShot20231213At610}
          alt=""
          src={productImageId}
        />
        <div className={styles.div}>{price}</div>
        <div className={styles.unisexLifestyle}>{productCategory}</div>
        <div className={styles.div1} style={divStyle}>
          {shoeModel}
        </div>
      </div>
    </div>
  );
};

export default SectionCard;
