import { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./ApparelVideo1.module.css";
import Video from "../components/Video";
import carouselStyles from "./Carousel.module.css";
import ProductCard from "../components/ProductCard"
import ReactGA from "react-ga4";

const ApparelVideo1 = () => {
  const navigate = useNavigate();

  const [showFeatured, setshowFeatured] = useState(false);
  const onClickShowFeatured = () => {
    setshowFeatured(true);
  };

  const onFrameContainerClick = useCallback(() => {
    ReactGA.event({
      category: "Click",
      action: "Click next on Apparel video 1"
    });

    navigate("/apparel-poll-2");
  }, [navigate]);

  const onProduct1Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 1 Fleece hoodie"
    });

    navigate("/sport-essentials-fleece-hoodie-pdp");
  }, [navigate]);

  const onProduct2Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 1 TECH SHERPA"
    });

    navigate("/rw-tech-sherpa-pullover-pdp");
  }, [navigate]);

  const onProduct3Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 1 Fresh Foam x840v1"
    });

    navigate("/fresh-foam-x-840v1-pdp");
  }, [navigate]);

  return (
    <div className={styles.apparelVideo1}>
      <div className={styles.v09044g40000cfej9sbc77ufnidgi2}>
        <Video videoUrl="apparel_video_1.mov"/>
      </div>
      {!showFeatured
      ?
      <>
        <div className={styles.sickPick}>Sick pick</div>

        <div className={styles.groupParent} >
          <div className={styles.rectangleParent} onClick={onClickShowFeatured}>
            <div className={styles.groupChild} />
            <div className={styles.viewFeaturedProducts}>
              View Featured Products
            </div>
          </div>
          <div className={styles.rectangleGroup}>
            <div className={styles.groupItem} />
            <div className={styles.nextQuestion} onClick={onFrameContainerClick}>
              Next
            </div>
          </div>
        </div>
      </>
      :
      <>
        <div className={carouselStyles.productCarousel}>
          <div className={carouselStyles.cards}>
            <ProductCard
              imageUrl="screen-shot-20231211-at-136-2@2x.png"
              productType="Mens Shirts"
              productName="Fleece hoodie"
              price="$74.99"
              onClick={onProduct1Click}
            />

            <ProductCard
              imageUrl="screen-shot-20231211-at-137-2@2x.png"
              productType="Mens Shirts"
              productName="TECH SHERPA"
              price="$74.99"
              onClick={onProduct2Click}
            />

            <ProductCard
              imageUrl="screen-shot-20231213-at-643-2@2x.png"
              productType="Mens Running"
              productName="Fresh Foam x840v1"
              price="$139.99"
              onClick={onProduct3Click}
            />
            </div>
        </div>
        <div className={styles.groupParent} >
          <div className={styles.rectangleParent} onClick={onFrameContainerClick}>
            <div className={styles.groupChildFeatured} />
            <div className={styles.nextQuestionFeatured}>Next</div>
          </div>
        </div>
      </>
      }
    </div>
  );
};

export default ApparelVideo1;
