import { useMemo } from "react";
import styles from "./VibeSection.module.css";

const VibeSection = ({
  questionText,
  activityPreferenceQuestio,
  propWidth,
  propAlignSelf,
  propWidth1,
}) => {
  const whatsYourVibeStyle = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const whichOneOfStyle = useMemo(() => {
    return {
      alignSelf: propAlignSelf,
      width: propWidth1,
    };
  }, [propAlignSelf, propWidth1]);

  return (
    <div className={styles.newBalanceLogo2Parent}>
      <img
        className={styles.newBalanceLogo2Icon}
        alt=""
        src="./new-balance-logo-2@2x.png"
      />
      <div className={styles.whatsYourVibeParent}>
        <div className={styles.whatsYourVibe} style={whatsYourVibeStyle}>
          {questionText}
        </div>
        <div className={styles.whichOneOf} style={whichOneOfStyle}>
          {activityPreferenceQuestio}
        </div>
      </div>
    </div>
  );
};

export default VibeSection;
