import { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./ShoeVideo1.module.css";
import carouselStyles from "./Carousel.module.css";
import ProductCard from "../components/ProductCard"
import Video from "../components/Video";
import ReactGA from "react-ga4";

const ShoeVideo1 = () => {
  const navigate = useNavigate();

  const onFrameContainerClick = useCallback(() => {
    ReactGA.event({
      category: "Click next",
      action: "Click next on shoes video 1"
    });

    navigate("/sneaker-poll-2");
  }, [navigate]);

  const [showFeatured, setshowFeatured] = useState(false);

  const onClickShowFeatured = () => {
    setshowFeatured(true);
  };

  const onNBKawhiClick = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Shoes Carousel 1 NB KAWHI 3"
    });

    navigate("/nb-kawhi-pdp");
  }, [navigate]);

  const onTwoWxyClick = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Shoes Carousel 1 TWO WXY v4"
    });

    navigate("/two-wxy-v4-pdp");
  }, [navigate]);

  const onMenShirtClick = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Shoes Carousel 1 Fleece hoodie"
    });

    navigate("/sport-essentials-fleece-hoodie-pdp");
  }, [navigate]);

  return (
    <div className={styles.shoeVideo1}>
      <div className={styles.v09044g40000cfej9sbc77ufnidgi2}>
        <Video videoUrl="shoes_video_1.mov"/>
      </div>

      {!showFeatured
      ?
      <>
        <div className={styles.slamDunk}>Slam dunk</div>

        <div className={styles.groupParent}>
          <div className={styles.rectangleParent} onClick={onClickShowFeatured}>
            <div className={styles.groupChild} />
            <div className={styles.viewFeaturedProducts}>
              View Featured Products
            </div>
          </div>
          <div className={styles.rectangleGroup} onClick={onFrameContainerClick}>
            <div className={styles.groupItem} />
            <div className={styles.nextQuestion}> Next</div>
          </div>
        </div>
      </>
      :
      <>
        <div className={carouselStyles.productCarousel}>
          <div className={carouselStyles.cards}>
            <ProductCard
              imageUrl="screen-shot-20231213-at-610-7@2x.png"
              productType="Mens Basketball"
              productName="NB KAWHI 3"
              price="$159.99"
              onClick={onNBKawhiClick}
            />

            <ProductCard
              imageUrl="screen-shot-20231213-at-612-1@2x.png"
              productType="Mens Basketball"
              productName="TWO WXY v4"
              price="$119.99"
              onClick={onTwoWxyClick}
            />

            <ProductCard
              imageUrl="screen-shot-20231211-at-136-2@2x.png"
              productType="Mens Shirts"
              productName="Fleece hoodie"
              price="$74.99"
              onClick={onMenShirtClick}
            />
          </div>
        </div>
        <div className={styles.groupParent}>
          <div className={styles.rectangleParent} onClick={onFrameContainerClick}>
            <div className={styles.groupChildFeatured} />
            <div className={styles.nextQuestionFeatured}>Next</div>
          </div>
        </div>
      </>
      }
    </div>
  );
};

export default ShoeVideo1;
