import { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./ApparelVideo3.module.css";
import carouselStyles from "./Carousel.module.css";
import ProductCard from "../components/ProductCard"
import Video from "../components/Video";
import ReactGA from "react-ga4";

const ApparelVideo3 = () => {
  const navigate = useNavigate();

  const [showFeatured, setshowFeatured] = useState(false);

  const onClickShowFeatured = () => {
    setshowFeatured(true);
  };
  const onViewVibeClick = useCallback(() => {
    ReactGA.event({
      category: "Click",
      action: "Click next on Apparel video 3"
    });

    navigate("/vibe-shop-apparel");
  }, [navigate]);

  const onProduct1Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 3 pre game sleeveless"
    });

    navigate("/klutch-x-nb-pre-game-chill-sleeveless-pdp");
  }, [navigate]);

  const onProduct2Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 3 Pre game hoodie"
    });

    navigate("/klutch-x-nb-pre-game-hoodie-pdp");
  }, [navigate]);

  const onProduct3Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 3 TWO WXY v4"
    });

    navigate("/two-wxy-v4-pdp");
  }, [navigate]);

  return (
    <div className={styles.apparelVideo3}>
      <div className={styles.v09044g40000cfej9sbc77ufnidgi2}>
        <Video videoUrl="apparel_video_3.mov"/>
      </div>
      {!showFeatured
      ?
      <>
        <div className={styles.loveIt}>Love it!!</div>
        <div className={styles.groupParent}>
          <div className={styles.rectangleParent} onClick={onClickShowFeatured}>
            <div className={styles.groupChild} />
            <div className={styles.viewFeaturedProducts}>
              View Featured Products
            </div>
          </div>
          <div className={styles.rectangleGroup} onClick={onViewVibeClick}>
            <div className={styles.groupItem} />
            <div className={styles.viewVibeShop}>View Vibe Shop</div>
          </div>
        </div>
      </>
      :
      <>
        <div className={carouselStyles.productCarousel}>
          <div className={carouselStyles.cards}>
            <ProductCard
              imageUrl="screen-shot-20231211-at-133-6@2x.png"
              productType="Mens Hoodies"
              productName="pre game sleeveless"
              price="$59.99"
              onClick={onProduct1Click}
            />

            <ProductCard
              imageUrl="screen-shot-20231211-at-133-7@2x.png"
              productType="Mens Basketball"
              productName="Pre game hoodie"
              price="$119.99"
              onClick={onProduct2Click}
            />

            <ProductCard
              imageUrl="screen-shot-20231213-at-612-1@2x.png"
              productType="Mens Basketball"
              productName="TWO WXY v4"
              price="$119.99"
              onClick={onProduct3Click}
            />
          </div>
        </div>
        <div className={styles.groupParent}>
          <div className={styles.rectangleParent} onClick={onViewVibeClick}>
            <div className={styles.groupChildFeatured} />
            <div className={styles.viewVibeShopFeatured}>View Vibe Shop</div>
          </div>
        </div>
      </>
      }

    </div>
  );
};

export default ApparelVideo3;
