import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./SectionCard7.module.css";
import { PollContext } from '../App';
import { useContext } from 'react';
import ReactGA from "react-ga4";

const SectionCard7 = () => {
  const navigate = useNavigate();
  const { selected, setSelected } = useContext(PollContext)

  const onProduct1Click = useCallback(() => {
    setSelected({...selected, ...{ sneaker_poll_3: 1 }});

    ReactGA.event({
      category: "Poll click",
      action: "Click poll 3 574 CORE"
    });


    navigate("/shoe-video-3");
  }, [navigate]);

  const onProduct2Click = useCallback(() => {
    setSelected({...selected, ...{ sneaker_poll_3: 2 }});

    ReactGA.event({
      category: "Poll click",
      action: "Click poll 3 9060"
    });

    navigate("/shoe-video-3");
  }, [navigate]);


  return (
    <div className={styles.frameParent}>
      <div
        className={styles.screenShot20231213At708Parent}
        onClick={onProduct1Click}
      >
        <img
          className={styles.screenShot20231213At708}
          alt=""
          src="screen-shot-20231213-at-708-1@2x.png"
        />
        <div className={styles.core}>574 CORE</div>
      </div>
      <div
        className={styles.screenShot20231213At612Parent}
        onClick={onProduct2Click}
      >
        <div className={styles.screenShot20231213At612} />
        <div className={styles.screenShot20231213At643} />
        <img
          className={styles.screenShot20231213At709}
          alt=""
          src="screen-shot-20231213-at-709-1@2x.png"
        />
        <div className={styles.div}>9060</div>
      </div>
    </div>
  );
};

export default SectionCard7;
