import { useCallback, useContext } from "react";
import { useNavigate } from "react-router-dom";
import VibeSection from "../components/VibeSection";
import CuratingYourVibeShopSection from "../components/CuratingYourVibeShopSection";
import styles from "./SneakerPoll2.module.css";
import { PollContext } from '../App';
import ReactGA from "react-ga4";

const SneakerPoll2 = () => {
  const navigate = useNavigate();
  const { selected, setSelected } = useContext(PollContext)

  const onProduct1Click = useCallback(() => {
    ReactGA.event({
      category: "Poll click",
      action: "Click poll 2 FRESH FOAM ROAV"
    });

    setSelected({...selected, ...{ sneaker_poll_2: 1 }});
    navigate("/shoe-video-2");
  }, [navigate]);

  const onProduct2Click = useCallback(() => {
    ReactGA.event({
      category: "Poll click",
      action: "Click poll 2 FRESH FOAM X 840V1"
    });

    setSelected({...selected, ...{ sneaker_poll_2: 2 }});
    navigate("/shoe-video-2");
  }, [navigate]);


  return (
    <div className={styles.sneakerPoll2}>
      <div className={styles.frameParent}>
        <VibeSection
          questionText="Whats your vibe?"
          activityPreferenceQuestio="Which one of these would you rather run in?"
          propWidth="290px"
          propAlignSelf="stretch"
          propWidth1="unset"
        />
        <CuratingYourVibeShopSection
          productImageDimensions="screen-shot-20231213-at-617-2@2x.png"
          productName="FRESH FOAM ROAV"
          imageDimensions="screen-shot-20231213-at-643-2@2x.png"
          productTitle="FRESH FOAM X 840V1"
          frameDivAlignItems="center"
          takeMeToWidth="166px"
          frameDivWidth="132px"
          lineDivAlignItems="center"
          lineDivWidth="169px"
          lineDivHeight="165px"
          curatingYourVibeWidth="145px"
          propWidth="155px"
          onProduct1Click={onProduct1Click}
          onProduct2Click={onProduct2Click}
        />
      </div>
    </div>
  );
};

export default SneakerPoll2;
