import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./ApparelSection.module.css";
import ReactGA from "react-ga4";

const ApparelSection = () => {
  const navigate = useNavigate();

  const onFrameContainer2Click = useCallback(() => {
    ReactGA.event({
      category: "Intro Select",
      action: "Click SNEAKERS on Intro"
    });

    navigate("/sneaker-poll-1");
  }, [navigate]);

  const onFrameContainer3Click = useCallback(() => {
    ReactGA.event({
      category: "Intro Select",
      action: "Click APPAREL on Intro"
    });

    navigate("/apparel-poll-1");
  }, [navigate]);

  return (
    <div className={styles.frameWrapper}>
      <div className={styles.frameParent}>
        <div
          className={styles.rectangleParent}
          onClick={onFrameContainer2Click}
        >
          <div className={styles.frameChild} />
          <div className={styles.sneakers}>SNEAKERS</div>
        </div>
        <div className={styles.rectangleGroup} onClick={onFrameContainer3Click}>
          <div className={styles.frameItem} />
          <div className={styles.apparel}>APPAREL</div>
        </div>
      </div>
    </div>
  );
};

export default ApparelSection;
