import { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./ApparelVideo2.module.css";
import carouselStyles from "./Carousel.module.css";
import ProductCard from "../components/ProductCard"
import Video from "../components/Video";
import ReactGA from "react-ga4";

const ApparelVideo2 = () => {
  const navigate = useNavigate();

  const [showFeatured, setshowFeatured] = useState(false);

  const onClickShowFeatured = () => {
    setshowFeatured(true);
  };

  const onNextQuestionClick = useCallback(() => {
    ReactGA.event({
      category: "Click",
      action: "Click next on Apparel video 2"
    });

    navigate("/apparel-poll-3");
  }, [navigate]);

  const onProduct1Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 2 Linear Fleece Top"
    });

    navigate("/athletics-linear-fleece-top-pdp");
  }, [navigate]);

  const onProduct2Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 2 French Terry Crewneck"
    });

    navigate("/unissentials-french-terry-crewneck-sweatshirt-pdp");
  }, [navigate]);

  const onProduct3Click = useCallback(() => {
    ReactGA.event({
      category: "Carousel click",
      action: "Click Apparel Carousel 2 574 Core"
    });

    navigate("/574-core-pdp");
  }, [navigate]);


  return (
    <div className={styles.apparelVideo2}>
      <div className={styles.v09044g40000cfej9sbc77ufnidgi2}>
        <Video videoUrl="apparel_video_2.mp4"/>
      </div>
      {!showFeatured
      ?
      <>
        <div className={styles.speakUp}>Speak up</div>
        <div className={styles.groupParent}>
          <div className={styles.rectangleParent} onClick={onClickShowFeatured}>
            <div className={styles.groupChild} />
            <div className={styles.viewFeaturedProducts}>
              View Featured Products
            </div>
          </div>
          <div className={styles.rectangleGroup}>
            <div className={styles.groupItem} />
            <div className={styles.nextQuestion} onClick={onNextQuestionClick}>
              Next
            </div>
          </div>
        </div>
      </>
      :
      <>
        <div className={carouselStyles.productCarousel}>
          <div className={carouselStyles.cards}>
            <ProductCard
              imageUrl="screen-shot-20231208-at-1120-3@2x.png"
              productType="Mens Hoodies"
              productName="Linear Fleece Top"
              price="$84.99"
              onClick={onProduct1Click}
            />

            <ProductCard
              imageUrl="screen-shot-20231211-at-148-3@2x.png"
              productType="Mens Shirts"
              productName="French Terry Crewneck"
              price="$119.99"
              onClick={onProduct2Click}
            />

            <ProductCard
              imageUrl="screen-shot-20231213-at-708-1@2x.png"
              productType="Mens Lifestyle"
              productName="574 Core"
              price="$89.99"
              onClick={onProduct3Click}
            />
          </div>
        </div>
        <div className={styles.groupParent}>
          <div className={styles.rectangleParent}>
            <div className={styles.groupChildFeatured} />
            <div className={styles.nextQuestionFeatured} onClick={onNextQuestionClick}>Next</div>
          </div>
        </div>
      </>
      }
    </div>
  );
};

export default ApparelVideo2;
