import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import VibeSection from "../components/VibeSection";
import CuratingYourVibeShopSection from "../components/CuratingYourVibeShopSection";
import styles from "./ApparelPoll1.module.css";
import { useContext } from 'react';
import { PollContext } from '../App';
import ReactGA from "react-ga4";

const ApparelPoll1 = () => {
  const navigate = useNavigate();

  const { selected, setSelected } = useContext(PollContext)

  const onProduct1Click = useCallback(() => {
    setSelected({...selected, ...{ apparel_poll_1: 1 }});
    ReactGA.event({
      category: "Poll click",
      action: "Click poll 1 FLEECE HOODIE"
    });

    navigate("/apparel-video-1");
  }, [navigate]);

  const onProduct2Click = useCallback(() => {
    setSelected({...selected, ...{ apparel_poll_1: 2 }});
    ReactGA.event({
      category: "Poll click",
      action: "Click poll 1 SHERPA PULLOVER"
    });

    navigate("/apparel-video-1");
  }, [navigate]);


  return (
    <div className={styles.apparelPoll1}>
      <div className={styles.frameParent}>
        <VibeSection
          questionText="Whats your vibe?"
          activityPreferenceQuestio="Which one of these would you rock?"
          propWidth="290px"
          propAlignSelf="stretch"
          propWidth1="unset"
        />
        <CuratingYourVibeShopSection
          productImageDimensions="screen-shot-20231211-at-136-2@2x.png"
          productName="FLEECE HOODIE"
          imageDimensions="screen-shot-20231211-at-137-2@2x.png"
          productTitle="SHERPA PULLOVER"
          frameDivAlignItems="flex-start"
          takeMeToWidth="130px"
          frameDivWidth="112px"
          lineDivAlignItems="flex-start"
          lineDivWidth="132px"
          lineDivHeight="171px"
          curatingYourVibeWidth="136px"
          propWidth="unset"
          propFlex="1"
          onProduct1Click={onProduct1Click}
          onProduct2Click={onProduct2Click}
        />
      </div>
    </div>
  );
};

export default ApparelPoll1;
