import { useCallback, useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import CardContainer2 from "../components/CardContainer2";
import CardContainer from "../components/CardContainer";
import CardContainer3 from "../components/CardContainer3";
import AdjustableContainer from "../components/AdjustableContainer";
import OfferShoppingContainer from "../components/OfferShoppingContainer";
import styles from "./VibeShopShoes.module.css";
import { PollContext } from '../App';
import { products } from '../components/products'
import VibeCard from '../components/VibeCard'
import ReactGA from "react-ga4";

const VibeShopShoes = () => {
  const { selected, setSelected } = useContext(PollContext)

  const navigate = useNavigate();

  const cards = () => {
    const selectedValues = `${selected['sneaker_poll_1']}${selected['sneaker_poll_2']}${selected['sneaker_poll_3']}`

    switch (selectedValues) {
      case '111': return [1, 3, 5, 7, 8, 9]
      case '112': return [1, 3, 6, 7, 8, 9]
      case '121': return [1, 4, 5, 7, 8, 9]
      case '122': return [1, 4, 6, 10, 11, 12]
      case '211': return [2, 3, 5, 10, 11, 12]
      case '212': return [2, 3, 6, 10, 11, 12]
      case '221': return [2, 4, 5, 13, 14, 15]
      case '222': return [2, 4, 6, 13,  14,  15]

      default:
        return [1, 3, 5, 7, 8, 9]
    }
  }

  const onGroupContainer12Click = useCallback(() => {
    ReactGA.event({
      category: "Promo Exit",
      action: "Click See Offer on Vibe Shop Shoes"
    });

    navigate("/promo-exit1");
  }, [navigate]);

  return (
    <div className={styles.vibeShopShoes}>
      <div className={styles.newBalanceLogo2Parent}>
        <img
          className={styles.newBalanceLogo2Icon}
          alt=""
          src="new-balance-logo-2@2x.png"
        />
        <div className={styles.yourVibeShop}>Your Vibe Shop</div>
      </div>
      <div className={styles.vibeShopShoesChild} />

      {
        cards().map(index => {
          const product = products[index-1];

          return (
            <VibeCard
              key={product['name']}
              imageUrl={product['image']}
              productName={product['name']}
              productUrl={product['url']}
            />
          );
        })
      }

      <div className={styles.vibeShopShoesChild} />

      <OfferShoppingContainer
        onGroupContainer12Click={onGroupContainer12Click}
      />

      <div className={styles.vibeShopShoesInner} />
      <div className={styles.vibeShopShoesInner} />
    </div>
  );
};

export default VibeShopShoes;
